.filter {
  margin: -1px;
  padding: 0 15px;
  position: relative;
  width: fit-content;
  height: fit-content;
  border: 1px solid rgb(20, 20, 255);
  border-right: none;
  border-bottom: none;
  border-radius: 12px 0 0 0;
  max-width: 95%;
}

.filter label {
  display: inline-block;
  padding: 10px;
  margin-left: -10px;
  font-size: 2em;
  color: #e9ecef;
  margin-top: -9px;
}

.filterSelect {
  display: inline-block;
  height: fit-content;
  padding: 10px;
  font-size: 1.2em;
  border-radius: 12px;
  margin: 9px 0;
}