.form {
  background-color: #30332E;
  width: calc(80% - 18px);
  margin: auto;
  border-radius: 0 0 20px 20px;
  border: 10px solid rgb(184, 184, 184);
  border-top: none;
}

.formMid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
}

.formInfo {
  background-color: #989795;
  width: fit-content;
  width: 170px;
  height: 120px;
  margin: 10px;
  border: 1px solid rgb(255, 255, 220);
  position: relative;
  border-radius: 12px;
  max-width: 85%;
}

.formInfo label {
  color: #0d6d74;
  font-size: 2em;
  padding: top;
  padding: 4px;
}

.formInfo input {
  width: 155px;
  margin: 4px;
  font-size: 1.5em;
  color: #0d6d74;
  max-width: 90%;
}

.formInfo button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: fit-content;
  height: fit-content;
  font-size: 1.5em;
  padding: 10px 5px;
  color: #0d6d74;
  cursor: pointer;
  max-width: 90%;
}

#cancelButton {
  color: rgb(248, 0, 95);
  width: 156px;
}

#addExpenseParent {
  background-color: #30332E;
  width: calc(80% - 18px);
  margin: auto;
  border-radius: 0 0 20px 20px;
  border: 10px solid rgb(184, 184, 184);
  border-top: none;
  padding-top: 10px;
}

#addExpense {
  display: block;
  margin-top: 0;
  font-size: 2em;
  color: #0d6d74;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  max-width: 100%;
}