.voRed {
  background-color: #a6a2a2;
  display: flex;
  margin: 10px;
  border: 1px solid rgb(255, 255, 220);
  border-radius: 10px;
}

.datum {
  padding-left: 10px;
  height: fit-content;
  margin: auto 0;
  align-items: center;
}

.desno {
  display: block;
  margin: auto 14px auto auto;
  background-color: #343a40;
  border-radius: 5px 10px 10px 5px;
  border: 1px solid rgb(255, 255, 220);
  width: 320px;
  color: #fff;
  height: 104.8px;
  overflow: auto;
}

.desno .title {
  padding-left: 10px;
  margin-top: 10px;
  overflow-x: auto;
}

.desno .amount {
  background-color: #353535;
  margin-left: 10px;
  width: fit-content;
  max-width: 200px;
  overflow-x: auto;
  padding: 7px;
  font-size: 1.2em;
  border: 1px solid rgb(255, 255, 220);
  border-radius: 12px;
  margin-top: -15px;
}

.desno::-webkit-scrollbar {
  width: 5px;
}

.desno::-webkit-scrollbar-track {
  background: #f1f1f1;
  margin: 10px 0;
}

.desno::-webkit-scrollbar-thumb {
  background: #888;
}

.desno::-webkit-scrollbar-thumb:hover {
  background: #555;
}


.amount::-webkit-scrollbar, .title::-webkit-scrollbar {
  height: 5px;
}

.amount::-webkit-scrollbar-track, .title::-webkit-scrollbar-track {
  background: #f1f1f1;
  margin: 0 10px;
}

.amount::-webkit-scrollbar-thumb, .title::-webkit-scrollbar-thumb {
  background: #888;
}

.amount::-webkit-scrollbar-thumb:hover, .title::-webkit-scrollbar-thumb:hover {
  background: #555;
}