.datum {
  text-align: center;
  background-color: #212529;
  width: 100px;
  border-radius: 12px;
  padding: 10px;
  border: 1px solid #fff;
  margin: 8px 15px;
  color: rgb(255, 255, 255);
  font-style: bold;
}

.datum .year {
  padding: 3px;
  border-bottom: 1px solid rgb(255, 255, 220);
}

.datum .month {
  padding: 3px;
  border-bottom: 1px solid rgb(255, 255, 220);
}

.datum .day {
  padding: 3px;
}